import React from 'react';
import Link from 'gatsby-link';
import penguinBanner from "./static/images/penguinBanner.png";
import penguinImg1 from "./static/images/penguinImg1.jpg";
import penguinImg2 from "./static/images/penguinImg2.jpg";
import penguinImg3 from "./static/images/penguinImg3.jpg";
import penguinImg4 from "./static/images/penguinImg4.jpg";
import volkswagenImg from "./static/images/volkswagenImg.jpg";
import vwDealershipImg from "./static/images/vwDealershipImg.jpg";

const penguin = () => (
  <div className="fullBox">

    <div className="insideBanner penguinpBanner">
      <div className="bg penguin"></div>
      <div className="midWrapper">
            <div className="rightSection">
              <div className="box">
                <div className="vMid">
                  <img src={penguinBanner} alt="" />
                </div>
              </div>
            </div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h1>PENGUIN</h1>
                  <h2>WEBSITE DESIGN</h2>
                  <p>Client : Penguin India<br />Work : Website Design<br />Agency : 22feet Tribal Worldwide</p>
                </div>
              </div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="penguinpContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="one">
            <li><h3>Homepage</h3>
              <img src={penguinImg1} alt="" /> </li>
              <li><h3>Young Readers Landing page</h3>
                <img src={penguinImg2} alt="" /> </li>
                <li><h3>Penguin Classics Landing page</h3>
                  <img src={penguinImg3} alt="" /> </li>
                  <li><h3>Classics Book Detail Page</h3>
                  <img src={penguinImg4} alt="" /> </li>
          </ul>
        </div>
      </div>
    </div>

    <div className="midWrapper nextPrevious textAlignCenter">
      <ul>
        <li className="back">
          <Link to="/vwtiguan">
            <div className="thumb"> <img src={volkswagenImg} alt="" /> </div>
            <div className="button"><span>Back</span></div>
          </Link>
        </li>
        <li className="next">
          <Link to="/vwdealership">
            <div className="thumb"> <img src={vwDealershipImg} alt="" /> </div>
            <div className="button"><span>next</span></div>
          </Link>
        </li>
      </ul>
    </div>


  </div>
)

export default penguin



